export default {
	data() {
		return {
			buyMaxCount: 0,
			buyRate:'0.00%',
			sellRate: '5.00%',
			dataIndex: 1,
			form:{
				usde:'',
				payPassword:''
			},
			secondPass:'',
			current: 0,
			items: [{
			                    value: '1',
			                    name: 'USDT',
								checked: 'true'
			                }
			            ],
			userDetail:''
		};
	},
	onLoad:function(){
		this.fetchUserDetail()
	},
	methods:{
		async fetchUserDetail(){
			const res  = await this.$http.get('/user/details')
			console.log(res)
			this.userDetail = res.data.data
			
			
		},
		quzheng:function(){
			this.form.usde = parseInt(this.form.usde/100)*100
		},
		selectLabel:function(e){
			this.dataIndex = e.target.dataset.index
			if (this.dataIndex == 2){
				
				
			}
			if (this.dataIndex == 1){
				
			}
		},
		async submit(){
			if (this.form.usde <100){
				uni.showToast({
					title: '最少數量100'
				});
				return
			}
			uni.showLoading({
				title: '提交中',
				mask: true
			});
			if (this.dataIndex ==1){
				const res = await this.$http.get('/udeal/buy_add',{data: this.form})
				this.form.usde = ''
				uni.hideLoading()
				uni.navigateTo({
					url: './orderList'
					
				});
				console.log(res)
				
			}else if (this.dataIndex ==2){
